/* border radius */
img {
    height: auto;
    object-fit: contain;
    object-position: center;
}

.product-img {
    width: 100%;
}

.input {
    outline: none;
    border: 1px solid #b6b6b6;
    padding: 7px 7px 8px 7px;
}

.select {
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
}

.search {
    width: 100%;
    min-width: 500px;
}

.button {
    padding-right: 10px;
    border: #225494;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: #225494;
    color: #fff;
}

.button:hover {
    background-color: #225394e7;
}

.button:focus {
    outline: none;
    background-color: #0f3361e7;
}

.seeBtn {
    border: 1px solid #225494;
    color: #225494;
}


.buyBtn {
    background-color: #225494;
    color: #fff;
}

.buyBtn:hover {
    background-color: #fff;
    border: 1px solid #E81504;
    color: #E81504;
}

.left {
    left: 0;
    width: 100%;
    height: 100vh;
    padding: 10px 25px;
}
.caRD {
    width: 100%;
    max-width: 200px;
    box-shadow:
    0.6px 2.8px 2.2px rgba(0, 0, 0, 0.02),
    1.5px 6.7px 5.3px rgba(0, 0, 0, 0.028),
    2.8px 12.5px 10px rgba(0, 0, 0, 0.035),
    4.9px 22.3px 17.9px rgba(0, 0, 0, 0.042),
    9.2px 41.8px 33.4px rgba(0, 0, 0, 0.05),
    22px 100px 80px rgba(0, 0, 0, 0.07)
    ;
    margin: 0 10px;
}

@media screen and (max-width: 600px) {
    .hiddin-search {
        display: none;
    }
    .mediaCard {
        flex-direction: column;
    }
    .mobileCarusel {
        height: 500px;
    }
    .mobileBtn {
        padding: 4px;
    }
  }

.scroll {
    overflow-y: scroll;
}

.modalWrapper {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.418);
    z-index: 9999;
    top: 0;
    left: 0;
}

.modalBody {
    width: 100%;
    max-width: 350px;
}

.maxW {
    max-width: 300px;
}

.link-unstyled {
    padding-bottom: 26px;
}

.link-unstyled:hover {
    text-decoration: none;
}


.loading {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 9999;
    background-color: #000000b9;
    display: flex;
    align-items: center;
    justify-content: center;
}

.lds-spinner {
    color: official;
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-spinner div {
    transform-origin: 40px 40px;
    animation: lds-spinner 1.2s linear infinite;
  }
  .lds-spinner div:after {
    content: " ";
    display: block;
    position: absolute;
    top: 3px;
    left: 37px;
    width: 6px;
    height: 18px;
    border-radius: 20%;
    background: #fff;
  }
  .lds-spinner div:nth-child(1) {
    transform: rotate(0deg);
    animation-delay: -1.1s;
  }
  .lds-spinner div:nth-child(2) {
    transform: rotate(30deg);
    animation-delay: -1s;
  }
  .lds-spinner div:nth-child(3) {
    transform: rotate(60deg);
    animation-delay: -0.9s;
  }
  .lds-spinner div:nth-child(4) {
    transform: rotate(90deg);
    animation-delay: -0.8s;
  }
  .lds-spinner div:nth-child(5) {
    transform: rotate(120deg);
    animation-delay: -0.7s;
  }
  .lds-spinner div:nth-child(6) {
    transform: rotate(150deg);
    animation-delay: -0.6s;
  }
  .lds-spinner div:nth-child(7) {
    transform: rotate(180deg);
    animation-delay: -0.5s;
  }
  .lds-spinner div:nth-child(8) {
    transform: rotate(210deg);
    animation-delay: -0.4s;
  }
  .lds-spinner div:nth-child(9) {
    transform: rotate(240deg);
    animation-delay: -0.3s;
  }
  .lds-spinner div:nth-child(10) {
    transform: rotate(270deg);
    animation-delay: -0.2s;
  }
  .lds-spinner div:nth-child(11) {
    transform: rotate(300deg);
    animation-delay: -0.1s;
  }
  .lds-spinner div:nth-child(12) {
    transform: rotate(330deg);
    animation-delay: 0s;
  }
  @keyframes lds-spinner {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

/**
 * Extracted from: SweetAlert
 * Modified by: Istiak Tridip
 */
.success-checkmark {
    width: 80px;
    height: 115px;
    margin: 0 auto;
}

.check-icon {
    width: 80px;
    height: 80px;
    position: relative;
    border-radius: 50%;
    box-sizing: content-box;
    border: 4px solid #4CAF50;
}



.check-icon::before {
    top: 3px;
    left: -2px;
    width: 30px;
    transform-origin: 100% 50%;
    border-radius: 100px 0 0 100px;
}

.check-icon::after {
    top: 0;
    left: 30px;
    width: 60px;
    transform-origin: 0 50%;
    border-radius: 0 100px 100px 0;
    animation: rotate-circle 4.25s ease-in;
}

.check-icon::before,
.check-icon::after {
    content: '';
    height: 100px;
    position: absolute;
    background: #FFFFFF;
    transform: rotate(-45deg);
}

.icon-line {
    height: 5px;
    background-color: #4CAF50;
    display: block;
    border-radius: 2px;
    position: absolute;
    z-index: 10;

}

.icon-line.line-tip {
    top: 46px;
    left: 14px;
    width: 25px;
    transform: rotate(45deg);
    animation: icon-line-tip 0.75s;
}

.icon-line.line-long {
    top: 38px;
    right: 8px;
    width: 47px;
    transform: rotate(-45deg);
    animation: icon-line-long 0.75s;
}


.icon-circle {
    top: -4px;
    left: -4px;
    z-index: 10;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    position: absolute;
    box-sizing: content-box;
    border: 4px solid rgba(76, 175, 80, .5);
}

.icon-fix {
    top: 8px;
    width: 5px;
    left: 26px;
    z-index: 1;
    height: 85px;
    position: absolute;
    transform: rotate(-45deg);
    background-color: #FFFFFF;
}



@keyframes rotate-circle {
    0% {
        transform: rotate(-45deg);
    }

    5% {
        transform: rotate(-45deg);
    }

    12% {
        transform: rotate(-405deg);
    }

    100% {
        transform: rotate(-405deg);
    }
}

@keyframes icon-line-tip {
    0% {
        width: 0;
        left: 1px;
        top: 19px;
    }

    54% {
        width: 0;
        left: 1px;
        top: 19px;
    }

    70% {
        width: 50px;
        left: -8px;
        top: 37px;
    }

    84% {
        width: 17px;
        left: 21px;
        top: 48px;
    }

    100% {
        width: 25px;
        left: 14px;
        top: 45px;
    }
}

@keyframes icon-line-long {
    0% {
        width: 0;
        right: 46px;
        top: 54px;
    }

    65% {
        width: 0;
        right: 46px;
        top: 54px;
    }

    84% {
        width: 55px;
        right: 0px;
        top: 35px;
    }

    100% {
        width: 47px;
        right: 8px;
        top: 38px;
    }
}