.chek {
    display: flex;
    border: solid 2px #225494;
    border-radius: 15px;
    margin-right: 10px;
    color: #225494;
    background-color: white;
    text-align: center;
    float: left;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in-out;
    padding: 5px 8px;
}

.chek-input[type=checkbox] {
    display: none;
}

.chek-input:checked+label {
    transition: all 0.3s ease-in-out;
    border: solid 2px dodgerblue;
    color: white;
    background-color: dodgerblue;
  
}
.chek:hover {
    cursor: pointer;
}
.chek-input[type="submit"]:hover{
  cursor:pointer;
  background-color:black;
  color:white;
  
}